.slider {
    float: right;
    margin-top: 100px;
    position: relative;
    width: 40%;
    height: 250px;
    box-sizing: border-box;
    display: flex;
    overflow: hidden;
}
.slider-img { 
    position: absolute;
    width: 40%;
    height: 250px;
    transition: all 0.1s;
}
 
