.header{
    top: 0px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    position: fixed;
    width: 100%;
    min-height: 50px;
    background-color: #303030;
}
.header_box{
    
    width: 60%;
    margin-left: 20px;
    margin-right: 10px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
}
.header_item{
    color: white;
}